import React from 'react';
import PropTypes from 'prop-types';
import './GameLog.css';

const GameLog = ({ history, stepNumber, someoneWon, handleJumpTo }) => (
  <ol className="last-moves">
    {history.map(({ id, lastMove: pastLastMove }) => {
      let message = '';
      if (id === 'move #0') {
        if (id === `move #${stepNumber}`) {
          message = `You are at game start`;
        } else {
          message = `Go to game start`;
        }
      } else if (id === `move #${stepNumber}`) {
        message = `You are at move ${
          pastLastMove ? `(${pastLastMove[0] + 1}, ${pastLastMove[1] + 1})` : ''
        }`;
      } else if (id === `move #${stepNumber - 1}`) {
        message = `Go to last move ${
          pastLastMove ? `(${pastLastMove[0] + 1}, ${pastLastMove[1] + 1})` : ''
        }`;
      } else if (id === `move #${stepNumber + 1}`) {
        message = `Go to next move ${
          pastLastMove ? `(${pastLastMove[0] + 1}, ${pastLastMove[1] + 1})` : ''
        }`;
      } else {
        message = `Go to ${id} ${
          pastLastMove ? `(${pastLastMove[0] + 1}, ${pastLastMove[1] + 1})` : ''
        }`;
      }

      const lastMoveWon =
        someoneWon !== '' &&
        `move #${stepNumber}` === history[history.length - 1].id;

      let strongClassName = 'selected';

      if (lastMoveWon) {
        strongClassName = 'winner';
      } else if (id === history[0].id) {
        strongClassName = '';
      }

      return (
        <li key={id}>
          <button
            className="last-move-button"
            onClick={() => handleJumpTo(id)}
            type="button"
            title={message}
          >
            {id === `move #${stepNumber}` ? (
              <strong className={strongClassName}>
                {lastMoveWon ? `${someoneWon} Won!` : message}
              </strong>
            ) : (
              message
            )}
          </button>
        </li>
      );
    })}
  </ol>
);

GameLog.propTypes = {
  history: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string.isRequired,
      squares: PropTypes.arrayOf(
        PropTypes.arrayOf(
          PropTypes.shape({ value: PropTypes.string.isRequired }).isRequired,
        ).isRequired,
      ).isRequired,
      lastMove: PropTypes.arrayOf(PropTypes.number),
    }).isRequired,
  ).isRequired,
  stepNumber: PropTypes.number.isRequired,
  someoneWon: PropTypes.string.isRequired,
  handleJumpTo: PropTypes.func.isRequired,
};

export default GameLog;
