import React from 'react';
import PropTypes from 'prop-types';
import './Header.css';

const Header = ({ isBrightThemeOn, toggleTheme }) => {
  return (
    <header className="header-bar">
      <h1 className="principal-title">Tic Tac Toe</h1>
      <button
        className="header-button"
        type="button"
        title="Change Theme"
        onClick={toggleTheme}
      >
        {isBrightThemeOn ? (
          <i className="material-icons">brightness_3</i>
        ) : (
          <i className="material-icons">brightness_5</i>
        )}
      </button>
    </header>
  );
};

Header.propTypes = {
  isBrightThemeOn: PropTypes.bool.isRequired,
  toggleTheme: PropTypes.func.isRequired,
};

export default Header;
