import React, { useState } from 'react';
import Header from './Header';
import TicTacToe from '../TicTacToe';
import './Container.css';

const Container = () => {
  const date = new Date();
  const hour = date.getHours();
  const useBightTheme = hour < 19 && hour > 7;
  const [isBrightThemeOn, setIsBrightThemeOn] = useState(useBightTheme);
  // const [isSidebarVisible, setSidebarVisible] = useState(false);

  const toggleTheme = () => {
    setIsBrightThemeOn(!isBrightThemeOn);
  };
  // const toggleSidebar = () => {
  //   setSidebarVisible(!isSidebarVisible);
  // };

  return (
    <div className={`container ${isBrightThemeOn ? 'light' : 'dark'}`}>
      <Header isBrightThemeOn={isBrightThemeOn} toggleTheme={toggleTheme} />
      <main className="content">
        <TicTacToe />
      </main>
    </div>
  );
};

export default Container;
